import { useEffect, useState } from 'react'
import apiProxy from '../../model/ApiProxy'
import styled from 'styled-components'
import Icon from '../Icon/Icon';
import UnderlineText from '../UnderlineText/UnderlineText'

const Wrapper = styled.div`
  .remove-hint{
    cursor: pointer;
    .text{
      display: flex;
      border:0;
    }
    span{
      margin-left:8px;
    }
  }
`

export default function RemoveHint({lang, baseToken, penalty, isB0Staked }) {
  const [text,setText] = useState('')
  useEffect(()=>{
    let label;
    if(isB0Staked){
      if(baseToken === 'BUSD'){
        label = `${lang['your-account-has-an-unsettled-balance-of']} ${penalty} BUSD ${['this-action-will-update-your']}`
      }else{
        label = `${lang['your-account-has-an-unsettled-balance-of']} ${penalty} BUSD ${['this-action-will-trigger-swapping']} ${baseToken} ${lang['to-update-your']}`
      }
    }else{
      label = `${lang['your-account-has-an-unsettled-balance-of']} ${penalty} BUSD ${['this-action-will-trigger-swapping']} ${baseToken} ${lang['to-update-your-busd-balance-to-reflect-the-unsettled-amount']}`
    }
    setText(label)
  },[isB0Staked,penalty,baseToken])
  return (
    <Wrapper>
      <div className='remove-hint'>
        <UnderlineText tip={text} width='332'>
          <Icon token='warn' /> <span>{lang['negative-unsettled-balance']}</span>
        </UnderlineText>
      </div>
    </Wrapper>

  )
}